import React from "react";

import './music-page.css';
import MusicPage from "./music-page";
import {connect} from "react-redux";
import {getAlbums} from "../../../redux/music-page-reducer";
import {getPlaylist} from "../../../redux/playlist-reducer";
import * as axios from "axios";

class MusicPageContainer extends React.Component {
    componentDidMount() {
        this.props.getAlbums();
    }

    render() {
        return <MusicPage albums={this.props.albums} getPlaylist={this.props.getPlaylist}/>
    }


};

let mapStateToProps = (state) => {
    return {
        albums: state.musicPage.albums
    }
}

export default  connect(mapStateToProps, {getAlbums,
    getPlaylist})(MusicPageContainer);

