import {albumsAPI, tracksAPI} from "../api/api";
import {setAlbums} from "./music-page-reducer";

const SET_PLAYLIST = 'SET_PLAYLIST';
const SET_CURRENT_ALBUM_ID = 'SET_CURRENT_ALBUM_ID'

let initialState = {
    tracks: [
        // {id: 0, key: 0, name: "На перекрестках жизни", url: "../../wwwRoot/mus/1.mp3"},
    ],
    idAlbum: '1'
}

const playlistReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_PLAYLIST:
            return {...state, tracks: action.tracks};
        case SET_CURRENT_ALBUM_ID:
            return {...state, idAlbum: action.idAlbum}
        default:
            return state;
    }
}

export const setPlaylist = (tracks) => ({type: 'SET_PLAYLIST', tracks})
export const setCurrentAlbumId = (idAlbum) => ({type: 'SET_CURRENT_ALBUM_ID', idAlbum})

export const getPlaylist = (idAlbum) => {
    return (dispatch) => {
        dispatch(setCurrentAlbumId(idAlbum))
        tracksAPI.getTracksFromAlbum(idAlbum).then(response => {
            if (response.data.success === true) {
                dispatch(setPlaylist(response.data.body.tracks));
            }
        });
    };
};

export default playlistReducer;
