let song = new Audio();
let id;

let playNewAudio = (link) => {
    song.src = link;
    song.play()
}

export let playPauseAudio = (newId, link) => {
    console.log(newId, link)
    if (!song.paused) {
        if (id === newId) {
            song.pause();
            return false
        }else{
            song.pause();
            playNewAudio(link);
            id = newId;
            return true
        }
    }else {
        if (id === newId) {
            song.play();
            return true
        }else{
            playNewAudio(link)
            id = newId;
            return true
        }
    }
}
