import {albumsAPI} from "../api/api";

let SET_ALBUMS = "SET_ALBUMS";

let initialState =  {
    albums: [
        // {idAlbum: 1, name: "На перекрестках жизни", cover: "../../wwwRoot/1.png"},
    ]
};

const musicPageReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_ALBUMS:
            return { ...state, albums: action.albums}
        default:
            return state;
    }
};

export const setAlbums = (albums) => ({type: SET_ALBUMS, albums});

export const getAlbums = () => {
    return (dispatch) => {
        albumsAPI.getAlbums().then(response => {
            if (response.data.success === true) {
                dispatch(setAlbums(response.data.body));
            }
        });
    };
};

export default musicPageReducer;
