import React from "react";
import './track.css'
import {playPauseAudio} from "../../../play/play";
import playImg from "../../../assets/icons/play.png"
import pauseImg from "../../../assets/icons/pause.png";

let img = {
    play: playImg,
    pause: pauseImg
    // play: '../../src/assets/icons/play.png',
    // pause: '../../src/assets/icons/pause.png'
};

export default class Track extends React.Component {

    id = this.props.id;
    link = this.props.link;

    playAudio = () => {
        this.props.setNewAudioObject(this.id, this.link);
    };

    render() {
        // console.log(this.props)
        return (
            <div id={this.id} className={'track'}>

                <img onClick={this.playAudio} className={'buttonPlay'} src={this.props.currentIdTrack.some(id => id === this.props.id) ? img.pause : img.play }  alt="play"/>
                <div className="name">{this.props.name}</div>
            </div>
        )
    }
};
