import React from "react";
import {BrowserRouter, Route} from "react-router-dom";

import "./app.css";
import Header from "./components/header/header";
import Sidebar from "./components/sidebar/sidebar";
import MusicPage from "./components/pages/music-page/music-page";
import Playlist from "./components/pages/playlist";
import store from "./redux/store";
import PlaylistContainer from "./components/pages/playlist-container";
import MusicPageContainer from "./components/pages/music-page/music-page-container";


const App = props => {
    return (
            <div className='app-wrapper'>
                <Header />
                <Sidebar/>
                <div className='app-content'>
                    <Route path='/music' render={ () => <MusicPageContainer /> } />
                    <Route path='/playlist' render={ () => <PlaylistContainer /> } />
                </div>
            </div>
    )
}

export default App;