import React from "react";
import Track from "../elements/track/track";

let Playlist = (props) => {
        return <div>
            <div className='tracks'>
                {
                    props.tracks.map(t => <Track key={t.idTrack} id={t.idTrack} name={t.name} link={t.link} audioPlay={props.audioPlay}
                                                 currentIdTrack={props.currentIdTrack} setNewAudioObject={props.setNewAudioObject}/>)
                }
            </div>
        </div>
    }


export default Playlist;
