import React from "react";
import Playlist from "./playlist";
import {setNewAudioObject} from "../../redux/play-reducer";
import {connect} from "react-redux";
import {getPlaylist} from "../../redux/playlist-reducer";

class PlaylistContainer extends React.Component {

    componentDidMount() {
        this.props.getPlaylist(this.props.idAlbum);
    }

    render() {
        return <Playlist tracks={this.props.tracks} audioPlay={this.props.audioPlay}
                         currentIdTrack={this.props.currentIdTrack} setNewAudioObject={this.props.setNewAudioObject}/>
    }
};

let mapStateToProps = (state) => {
    return {
        tracks: state.playlist.tracks,
        idAlbum: state.playlist.idAlbum,
        currentIdTrack: state.currentPlay.id
    }
};

export default connect(mapStateToProps, {
    setNewAudioObject,
    getPlaylist
})(PlaylistContainer);
