import {playPauseAudio} from "../play/play";

let SET_AUDIO_OBJECT = "SET_AUDIO_OBJECT";

let initialState =  {
    play: false,
    id: []
};

const playReducer = (state = initialState, action) => {
    switch(action.type) {
        case SET_AUDIO_OBJECT:
            return {
                ...state,
                id: action.play
                    ? [action.id]
                    : [],
            }
        default:
            return state;
    }
}
const setAudioObject = (id, play) => ({
    type: 'SET_AUDIO_OBJECT',
    id,
    play
});

export const setNewAudioObject = (id, link) => {
    return (dispatch) => {
        let play =  playPauseAudio(id, link);
        dispatch(setAudioObject(id, play));
    };
};

export default playReducer;
