import React from "react";
import {NavLink} from "react-router-dom";

const Album = props => {
    let idAlbum =  props.idAlbum;

    let NowAlbumId = () =>{
        props.getPlaylist(idAlbum);

    };

    return (
        <div id={props.idAlbum} key={props.idAlbum} className={'album'}>
            <NavLink to={'/playlist'}><div onClick={NowAlbumId}  className="cover"><img src={props.cover} alt="cover"/></div></NavLink>
            <div className="name">{props.name}</div>
        </div>
    )
}

export default Album
