import React from "react";
import Album from "../../elements/album";

import './music-page.css';

let MusicPage = (props) => {
    return <div>
        <div className='albums'>
            {
                props.albums.map(a => <Album getPlaylist={props.getPlaylist} key={a.idAlbum} idAlbum={a.idAlbum}
                                             name={a.name} cover={a.linkCover}/>)
            }
        </div>
    </div>
};

export default MusicPage;
